
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Radio from 'vux/src/components/radio/index.vue'

export default {
components: {
ViewBox,
XHeader,
Group,
CheckIcon,
Masker,
Checker,
XTextarea,
CheckerItem,
Radio
},
data () {
return {
index: 0,
opinion: '',
qType1: false,
qType2: false,
qType3: false,
qType4: false,
selectType: ''
}
},
mounted () {
},
methods: {
selectQType (type) {
this.reset()
this['qType' + type] = true
this.selectType = type
},
reset () {
this.qType1 = false
this.qType2 = false
this.qType3 = false
this.qType4 = false
},
submit () {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/feedback.do'
let token = self.$store.state.userInfo.token
let data = {
action: 'init_request',
path: path,
params: {
userToken: token,
qType: this.selectType,
opinion: this.opinion
}
}

this.$vux.loading.show({
text: 'Loading'
})
if (this.selectType !== '') {
if (this.opinion !== '') {
self.$store.dispatch('normalRequest', data).then(res => {
if (res.response === 'success') {
this.$vux.toast.text('意见反馈成功')
this.$router.replace('/more')
this.$store.state.tabItem = 3
} else {
this.$vux.toast.text(res.data)
}

this.$vux.loading.hide()
})
} else {
this.$vux.toast.text('请填写意见内容')
}
} else {
this.$vux.toast.text('请选择一种意见类型')
}
this.$vux.loading.hide()
}
}
}



